<template>
  <div id="myinfo">
    <!-- 导航条 -->
    <mt-header title="修改资料" class="commonBGColor">
      <mt-button  icon="back" slot="left" @click="handleBack">返回</mt-button>
    </mt-header>

    <div id = "avatar_wrap">
      <img slot="icon" :src="avatarPath" width="90" height="90" class="userAvatar">
      <!-- <upload></upload> -->
      <!-- <mt-button  type="default" size="small" @click="openPicker()" class="upBtn">上传头像</mt-button> -->

      <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
            <a class="upBtn">上传头像</a>
      </upload>
    </div>

    <mt-field label="昵称" placeholder="请输入昵称" v-model="trueName"></mt-field>
    <!-- <mt-field label="公司/机构" disabled v-model="mechanism"></mt-field> -->
    <mt-field label="个性签名" placeholder="请输入个性签名" v-model="signContent"></mt-field>
    <!-- <mt-field label="地址" placeholder="请输入地址" type="tel" v-model="address"></mt-field> -->
    <!-- <mt-field label="生日" placeholder="1990-01-01" type="tel" v-model="birthday" @click="openPicker()"></mt-field> -->

    <mt-button  type="primary" size="large" @click="handleModeify()" class="modeifyBtn">确认修改</mt-button>

  </div>

</template>

<script>
import {Toast,Indicator,MessageBox} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import upload from '../../components/common/upload'
export default {
  data(){
    return{
      imgBaseUrl,
      isLogin:false,
      userID:null,
      userToken:null,
      trueName:'',//昵称
      signContent:'', //个性签名
      mechanism:'郴房网络咨询有限公司',
      address:'', //地址
      birthday:'', //生日
      avatarPath:'', //头像
      pickerValue:'',
      thumbnail : null,
      progress : 0 //上传进度
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    this.getDate()
  },
  mounted(){
  },
  components:{
    upload,
  },
  methods: {
    handleBack(){
      this.$router.go(-1);
    },
    handleUpImg(){

    },
    handleModeify(){
      let params = {
        signContent:this.signContent,
        address:this.address,
        birthday:this.birthday,
        trueName:this.trueName,
        avatarPath:this.avatarPath,
      }
      // console.log(params)
      MessageBox.confirm('确定修改吗！').then(action => {
        //  post 更新用户信息
        this.upDateUser(params)
      }, response => {
      });
    },
    // post 更新用户信息
    upDateUser(params){
      let getUrl = this.CFWConfig.APIHost + '/v1/user/upuser?token='+this.userToken
      this.$http.post(getUrl,params).then(response => {
        this.showMsg(response.data.msg)
        if (response.data.code == 200) {
          setTimeout(() => {
            this.handleBack()
          }, 1500);
        }
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    getDate(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v1/user/getuinfo?token='+this.userToken
      this.$http.post(getUrl).then(response => {
        if (response.data.code == 200) {
          let result = response.data.result
          this.trueName = result.trueName //昵称
          this.signContent = result.signContent  //个性签名
          this.avatarPath = result.avatarPath
          //this.address = response.data.signContent //地址
          //this.birthday:'', //生日
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        this.progress = progress < 100 ? progress : 0;
        console.log(this.progress)
    },
    uploadComplete(status, result, flag) {
        if (status == 200) { //
            this.thumbnail = imgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
            this.avatarPath = this.thumbnail
        } else {
            //失败处理
        }
    },
    openPicker(){
    },
    showMsg(msg){
      Toast({
        message:msg,
        duration: 800
      });
    },
  }
}

</script>

<style>
/*引入公共样式*/
@import '../../style/common/style.css';

/*导航标题*/
#avatar_wrap{
  height: 150px;
  margin-top: 10px;
  display: flex;
  flex-direction:column;
  align-items:center;
}

#avatar_wrap .userAvatar{
  margin-bottom: 20px;
  border-radius:45px;
}

#avatar_wrap .upBtn{
  width: 80px;
  height: 30px;
  /*border: 1px solid red;*/
  padding: 5px;
  color: #FFF;
  font-weight: bold;
  background-color: #0091ea;
  border-radius: 5px;
}

.modeifyBtn{
  width: 80%;
  margin: 20px auto;
}

.progress{
  height: 30px;
  background-color: red;
}

</style>



